.container {
  width: 430px;
  height: 940px;
  /* background-color: skyblue; */
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;

  /* justify-content: center; */
}

.main22 {
  height: 174px;
  width: 430px;
  color: white;
  text-align: center;
  font-size: 30px;
  margin-top: 0;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('https://raw.githubusercontent.com/subGit123/lala/refs/heads/main/img/date.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.card {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  margin-top: 20px;
  gap: 10px;
  z-index: 1;
  margin-bottom: 20px;
  /* gap: 10px;
  width: 200px;
  height: 200px; */
}

.card1,
.card2,
.card3,
.card4 {
  display: flex;
  flex-direction: column;
  width: 205px;
  border: solid 1px black;
  cursor: pointer;
  border-radius: 15px;
}

.caredText {
  text-align: center;
}

.card1:hover,
.card2:hover,
.card3:hover,
.card4:hover {
  transform: scale(1.1);
}

.modal {
  width: 400px;
  height: 540px;
  background-color: whitesmoke;
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 130px;
  z-index: 999;
  border-radius: 15px;
}

.tripmodal {
  width: 400px;
  height: 502px;
  background-color: whitesmoke;
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 130px;
  z-index: 99;
  border-radius: 15px;
}

.tripModalJeju {
  width: 360px;
  height: 402px;
  background-color: #222222;
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 220px;
  z-index: 999;
  border-radius: 15px;
}

.modal22 {
  display: flex;
  flex-direction: row;
}

.video_modal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  margin-top: 20px;
  margin-bottom: -70px;
}

.modal00 {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-self: flex-end;
  margin-bottom: 10px;
}

.modal33 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
  width: 400px;
  position: absolute;
  top: 50%;
}

.modalimg {
  width: 400px;
  height: 440px;
  border-radius: 15px;
}

.modalimg22 {
  width: 380px;
  height: 180px;
  border-radius: 15px;
}

.footer {
  background-color: #222222;
  width: 430px;
  height: 170px;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  margin-top: 15px;
  margin-bottom: 0;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.video_closeicon {
  width: 35px;
  height: 35px;
  margin-bottom: 10px;
  color: white;
  z-index: 2;
}

.closeicon {
  width: 30px;
  height: 30px;
  position: relative;
  top: 10px;
  right: -160px;
  margin-bottom: 10px;
}

.closeicon22 {
  width: 30px;
  height: 30px;
  position: relative;
  top: 10px;
  right: 30px;
}

.arrowicon {
  background-color: white;
  text-align: center;
  height: 30px;
  width: 30px;
}

.ani {
  width: 350px;
  height: 502px;
  border-radius: 15px;
}

.whiteLine {
  width: 350px;
  height: 5px;
  background-color: white;
  position: relative;
  bottom: 35px;
}

.redLine {
  width: 0;
  height: 5px;
  background-color: #dd2c00;
  position: relative;
  bottom: 39px;
  animation: growRight 35s ease-in-out forwards;
}

@keyframes growRight {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
