.container {
  max-width: 430px;
  height: 832px;
  background-color: pink;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mainC {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 30%;
  width: 80%;
  border: 1px solid grey;
  border-radius: 15%;
  background-color: antiquewhite;
  margin-bottom: 200px;
}

.h2font {
  font-size: 36px;
  font-weight: 800;
}

.img {
  width: 150px;
  height: 150px;
  /* margin-top: -80px; */
}

.font {
  font-size: 20px;
  font-weight: 800;
}

.dropdownContainer {
  background-color: white;
}

.btn {
  /* border-radius: 10%; */
  background-color: white;
}

li {
  text-decoration: none;
}

.dropdown {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

/* 리스트 항목 스타일 */
.dropdown li {
  padding: 10px; /* 항목 내부 여백 */
  border-bottom: 1px solid #ddd; /* 항목 간의 구분선 */
  cursor: pointer; /* 클릭할 수 있음을 나타내는 커서 */
  transition: background-color 0.3s; /* 호버 효과의 부드러운 전환 */
}

/* 호버 효과 */
.dropdown li:hover {
  background-color: #f0f0f0; /* 호버 시 배경색 변경 */
}
