.container {
  width: 430px;
  height: 932px;
  background-color: skyblue;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  background-image: url('https://raw.githubusercontent.com/subGit123/lala/refs/heads/main/img/bg.jpg');
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  /* justify-content: center; */
}

.header {
  height: 174px;
  width: 430px;
  color: white;
  text-align: center;
  font-size: 30px;
  margin-top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('https://raw.githubusercontent.com/subGit123/lala/refs/heads/main/img/cake.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  position: relative;
}

.첫만남con {
  width: 430px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}

.첫만남 {
  height: 80px;
  width: 80px;
  color: pink;
}

.첫데이트con {
  width: 430px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin-top: 30px;
}

.첫데이트 {
  height: 80px;
  width: 80px;
  color: springgreen;
}

.백일con {
  width: 430px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: -30px;
  margin-left: -240px;
}

.백일 {
  height: 80px;
  width: 80px;
  color: skyblue;
}

.이백일con {
  width: 430px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 40px;
  margin-right: 100px;
}

.이백일 {
  height: 80px;
  width: 80px;
  color: blueviolet;
}

.삼백일con {
  /* width: 430px; */
  display: flex;
  margin-left: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.삼백일 {
  height: 80px;
  width: 80px;
  color: red;
}

.사백일con {
  /* width: 430px; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.사백일 {
  height: 80px;
  width: 80px;
  color: aquamarine;
}

.일년con {
  width: 430px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 200px;
  margin-bottom: 0;
}

.일년 {
  height: 80px;
  width: 80px;
  color: slategrey;
}

:is(.일년, .사백일, .삼백일, .이백일, .백일, .첫데이트, .첫만남) {
  animation: bubble 1s ease;
  animation-fill-mode: forwards;
}

.일년 {
  animation-delay: 0.5s;
}

.사백일 {
  animation-delay: 1s;
}

.삼백일 {
  animation-delay: 1.5s;
}

.이백일 {
  animation-delay: 2s;
}

.백일 {
  animation-delay: 2.5s;
}

.첫데이트 {
  animation-delay: 3s;
}

.첫만남 {
  animation-delay: 3.5s;
}

@keyframes bubble {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-50%);
  }
}

.modal {
  width: 400px;
  height: 702px;
  background-color: whitesmoke;
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  top: -650px;
  z-index: 2;
  border-radius: 15px;
  animation: fadein 3s;
  -moz-animation: fadein 3s;
  -webkit-animation: fadein 3s;
  -o-animation: fadein 3s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modalimg {
  width: 400px;
  height: 340px;
  border: solid 1px black;
}

